import { getYMDFormateTime } from '../../../js/utility.js'

export default {
  name: 'notice-cell',
  components: {},
  data() {
    return {}
  },
  computed: {
    update_time() {
      return getYMDFormateTime(this.item.update_time);
    },
  },
  props: {
    item: {
      "title": "",
      "update_time": "",
      "sub_content": "",
    },
  },
  mounted() {}
}




