import { AjaxHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
import PlateType from '../../../js/enum.js'
import NoticeCell from '../components/NoticeCell'
import { getYMDFormateTime, isArrayType } from "../../../js/utility.js"
import { mapState } from 'vuex'

export default {
  name: 'notice',
  components: {
    NoticeCell,
  },
  computed: {
    ...mapState([
      'crmListInfo'
    ]),
  },
  beforeRouteLeave(to, from, next) {
    // console.log('beforeRouteLeave-to--' + to.name + '---from---' + from.name);
    if (to.name === 'articledetails') { //离开页面返回上层，需要重置数据
      this.$store.commit('updateCrmListInfo', {
        type: PlateType.Notice, //平台类型与type相同 进行赋值
        list: this.listItem,
        p: this.p,
        scroll: this.$refs.my_scroller.getPosition().top,
        add: false,
        loadingState: this.$refs.my_scroller.loadingState
      });
    } else { //记录所有状态
      this.$store.commit('updateCrmListInfo', {
        type: '', //平台类型与type相同 进行赋值
        list: [],
        p: 1,
        scroll: 0,
        add: false,
        loadingState: 0
      });

    }
    next();
  },
  data() {
    return {
      p: 1,
      listItem: []
    }
  },
  mounted() {
    if (this.crmListInfo.type === PlateType.Notice) {
      this.listItem = this.crmListInfo.list;
      this.p = this.crmListInfo.p;
      this.$nextTick(() => {
        this.$refs.my_scroller.resize();
        this.$refs.my_scroller.scrollTo(0, this.crmListInfo.scroll, false);
        if (this.crmListInfo.loadingState === 2) {
          this.$refs.my_scroller.finishInfinite(true);
        }
      });
    }
  },
  methods: {
    infinite(done) {
      this.getList(done);
    },
    refresh(done) {
      this.p = 1;
      this.getList(done);
    },
    getList(done) {
      AjaxSilentHttp('POST', 'm=announcement&a=index', { 'p': this.p }, this, function(res, wself, success) {
        if (success && isArrayType(res.list)) {
          if (wself.p === 1) {
            wself.listItem = res.list;
          } else {
            wself.listItem = wself.listItem.concat(res.list);
          }
          if (typeof done === 'function') {
            if (wself.p === 1 && res.list.length < 10) {
              done(true);
            } else if (res.list.length === 0) {
              done(true);
            } else {
              done();
            }
          }
          wself.p = wself.p + 1;
        }
      });
    },
    enterNoticeDetail(item) {
      this.$router.push({
        name: 'articledetails',
        params: {
          id: item.announcement_id
        }
      })
    }
  }
}




